input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */ }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.scroll-lock-ios {
  position: fixed;
  overflow: hidden; }

.scroll-lock {
  overflow: hidden; }

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .no-scrollbar::-webkit-scrollbar {
    display: none; }

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important; }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px; }
  @media (max-width: 1439px) {
    .container {
      padding: 0 32px; } }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }

.btn {
  display: inline-flex;
  margin: 10px 0;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease; }
  .btn--red {
    background-color: #ff1553; }
  .btn:disabled {
    opacity: 0.5;
    pointer-events: none; }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn:hover, .btn:focus {
      opacity: 0.8; } }

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
    @media (max-width: 767px) {
      .modal__wrapper {
        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    padding: 40px;
    background-color: #ffffff; }
    @media (max-width: 767px) {
      .modal__content {
        width: 100%;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 22px;
    right: 22px;
    z-index: 1;
    width: 40px;
    height: 40px;
    padding: 0;
    background: rgba(255, 255, 255, 0);
    border: none;
    cursor: pointer; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    transition: none; }
  .modal.is-active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

